'use client';
import { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { type RecentTrade } from '@/lib/bybit-api';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Skeleton } from '@/components/ui/skeleton';
import { format } from 'date-fns';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';
import { RefreshCw, Wifi, WifiOff, AlertCircle } from 'lucide-react';

// Constants
const REFRESH_INTERVAL = 3000; // 3 seconds
const NEW_TRADE_HIGHLIGHT_DURATION = 1500; // 1.5 seconds
const MAX_RETRY_ATTEMPTS = 3;
const RETRY_DELAY_MULTIPLIER = 1.5; // Exponential backoff multiplier
const INITIAL_RETRY_DELAY = 1000; // 1 second
const MAX_VISIBLE_TRADES = 50; // Limit visible trades for performance
const LOADING_ROW_COUNT = 15; // Number of skeleton rows to show during loading

// Utility function to format timestamp with error handling
const formatTime = (timestamp: string | number | undefined): string => {
  if (timestamp === undefined || timestamp === null) {
    return '...';
  }
  try {
    const numericTimestamp = Number(timestamp);
    if (isNaN(numericTimestamp) || !isFinite(numericTimestamp) || numericTimestamp <= 0) {
      return 'Invalid Time';
    }
    return format(new Date(numericTimestamp), 'HH:mm:ss');
  } catch (e) {
    console.error("Error formatting time:", e);
    return 'Invalid Time';
  }
};

// Enhanced utility function to format amount with appropriate precision
const formatAmount = (amountStr: string | undefined): string => {
    if (!amountStr) return '0.000';
    const amount = parseFloat(amountStr);
    if (isNaN(amount)) return '0.000';
    
    // Handle very large amounts
    if (amount >= 1_000_000) {
        return `${(amount / 1_000_000).toFixed(2)}M`;
    }
    if (amount >= 1000) {
        return `${(amount / 1000).toFixed(2)}K`;
    }
    
    // Handle very small amounts
    if (amount < 0.01 && amount > 0) {
      return amount.toFixed(5);
    }
    if (amount < 0.0001 && amount > 0) {
      return amount.toFixed(6);
    }
    
    return amount.toFixed(3);
};

// Loading skeleton component with configurable row count
const LoadingRows = ({ count = LOADING_ROW_COUNT }: { count?: number }) => (
  Array.from({ length: count }).map((_, i) => (
      <TableRow key={i} className="text-xs" aria-hidden="true">
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
      </TableRow>
  ))
);

// Props interface with additional optional props
interface RecentTradesProps {
  symbol: string;
  maxTrades?: number;
  refreshInterval?: number;
  autoRefresh?: boolean;
  className?: string;
}

// Main component
export function RecentTrades({ 
  symbol, 
  maxTrades = MAX_VISIBLE_TRADES,
  refreshInterval = REFRESH_INTERVAL,
  autoRefresh = true,
  className
}: RecentTradesProps) {
  const [trades, setTrades] = useState<RecentTrade[]>([]);
  const [newTradeIds, setNewTradeIds] = useState<Set<string>>(new Set());
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [retryCount, setRetryCount] = useState(0);
  const [isOnline, setIsOnline] = useState(true);
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [pauseAutoRefresh, setPauseAutoRefresh] = useState(false);
  
  const prevTradeIds = useRef<Set<string>>(new Set());
  const intervalId = useRef<NodeJS.Timeout | null>(null);
  const timeoutIds = useRef<NodeJS.Timeout[]>([]);
  const isMounted = useRef(true);
  const abortControllerRef = useRef<AbortController | null>(null);
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  // Cleanup function for timeouts and intervals
  const cleanup = useCallback(() => {
    if (intervalId.current) {
      clearInterval(intervalId.current);
      intervalId.current = null;
    }
    
    timeoutIds.current.forEach(id => clearTimeout(id));
    timeoutIds.current = [];
    
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
      abortControllerRef.current = null;
    }
  }, []);
  
  const fetchData = useCallback(async (isInitialLoad = false, isManualRefresh = false) => {
    if (!isMounted.current || (!isOnline && !isManualRefresh)) return;
    
    if (isManualRefresh) {
        setIsRefreshing(true);
    } else if (isInitialLoad) {
      setIsLoading(true);
      setError(null);
      setRetryCount(0);
    }
    
    abortControllerRef.current?.abort();
    abortControllerRef.current = new AbortController();
    
    try {
      const response = await fetch(`/api/trades?symbol=${symbol}`, {
        signal: abortControllerRef.current.signal,
      });

      if (!response.ok) {
        throw new Error(`API request failed with status ${response.status}`);
      }

      const data = await response.json();
      const recentTrades = data.trades as RecentTrade[];
      
      if (!isMounted.current) return;
      
      const newTradesFound = recentTrades.filter(t => !prevTradeIds.current.has(t.execId) && prevTradeIds.current.size > 0);
      
      if(newTradesFound.length > 0) {
          const newIds = new Set(newTradesFound.map(t => t.execId));
          setNewTradeIds(newIds);
          
          const timeoutId = setTimeout(() => {
              if (isMounted.current) {
                setNewTradeIds(currentIds => {
                    const updatedIds = new Set(currentIds);
                    newIds.forEach(id => updatedIds.delete(id));
                    return updatedIds;
                });
              }
          }, NEW_TRADE_HIGHLIGHT_DURATION);
          
          timeoutIds.current.push(timeoutId);
          
          if (scrollAreaRef.current && scrollAreaRef.current.scrollTop === 0) {
            scrollAreaRef.current.scrollTo({ top: 0, behavior: 'smooth' });
          }
      }
      
      setTrades(recentTrades);
      prevTradeIds.current = new Set(recentTrades.map(t => t.execId));
      setLastUpdated(new Date());
      setError(null);
      setRetryCount(0);
    } catch (e) {
      if (!isMounted.current) return;
      
      const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
      
      if (e instanceof Error && e.name === 'AbortError') {
        console.log("Request was aborted");
        return;
      }
      
      console.error("Failed to fetch recent trades:", e);
      
      if (isInitialLoad || isManualRefresh) {
        setError(errorMessage);
      }
      
      if (retryCount < MAX_RETRY_ATTEMPTS && isOnline) {
        const delay = INITIAL_RETRY_DELAY * Math.pow(RETRY_DELAY_MULTIPLIER, retryCount);
        setRetryCount(prev => prev + 1);
        
        const retryTimeoutId = setTimeout(() => {
          if (isMounted.current) {
            fetchData(false);
          }
        }, delay);
        
        timeoutIds.current.push(retryTimeoutId);
      } else if (retryCount >= MAX_RETRY_ATTEMPTS) {
        setError(`Failed to fetch data after ${MAX_RETRY_ATTEMPTS} attempts. Please try again later.`);
      }
    } finally {
      if (isMounted.current) {
        if (isInitialLoad) setIsLoading(false);
        if (isManualRefresh) setIsRefreshing(false);
      }
    }
  }, [symbol, retryCount, isOnline]);
  
  useEffect(() => {
    isMounted.current = true;
    
    const handleOnline = () => {
      setIsOnline(true);
      setError(null);
      fetchData(true);
    };
    
    const handleOffline = () => {
        setIsOnline(false);
        setError('Network connection lost. Please check your internet connection.');
        cleanup();
    };
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    fetchData(true);

    return () => {
      isMounted.current = false;
      cleanup();
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, [symbol]);

  useEffect(() => {
    cleanup();
    if (isOnline && autoRefresh && !pauseAutoRefresh) {
      intervalId.current = setInterval(() => fetchData(false), refreshInterval);
    }
    
    return cleanup;
  }, [isOnline, autoRefresh, pauseAutoRefresh, fetchData, cleanup, refreshInterval]);


  const handleManualRefresh = useCallback(() => {
    fetchData(false, true);
  }, [fetchData]);

  const toggleAutoRefresh = useCallback(() => {
    setPauseAutoRefresh(prev => !prev);
  }, []);

  const visibleTrades = useMemo(() => {
    return trades.slice(0, maxTrades);
  }, [trades, maxTrades]);

  const formatLastUpdated = useCallback((date: Date | null): string => {
    if (!date) return '';
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit' });
  }, []);

  const renderContent = useMemo(() => {
    if (isLoading) {
      return <LoadingRows />;
    }
    
    if (error && trades.length === 0) {
      return (
        <TableRow>
          <TableCell colSpan={3} className="text-center p-4" role="alert">
            <div className="flex flex-col items-center space-y-2">
              <AlertCircle className="h-5 w-5 text-red-500" />
              <p className="text-sm text-red-500">{error}</p>
              <Button 
                onClick={handleManualRefresh} 
                variant="outline" 
                size="sm"
                disabled={isRefreshing || !isOnline}
              >
                <RefreshCw className={cn("h-4 w-4 mr-1", isRefreshing && "animate-spin")} />
                Retry
              </Button>
            </div>
          </TableCell>
        </TableRow>
      );
    }
    
    if (visibleTrades.length > 0) {
      return visibleTrades.map((trade) => {
        const isNew = newTradeIds.has(trade.execId);
        if (!trade.qty || !trade.price || !trade.execTime) return null;
        
        return (
            <TableRow 
                key={trade.execId} 
                className={cn(
                    "text-xs font-mono transition-all duration-1000",
                    isNew && trade.side === 'Buy' && 'bg-green-500/30',
                    isNew && trade.side === 'Sell' && 'bg-red-500/30'
                )}
            >
                <TableCell className="p-2">{formatTime(trade.execTime)}</TableCell>
                <TableCell className={`p-2 font-medium ${trade.side === 'Buy' ? 'text-chart-2' : 'text-chart-5'}`}>
                  {parseFloat(trade.price).toFixed(2)}
                </TableCell>
                <TableCell className="p-2 text-right">{formatAmount(trade.qty)}</TableCell>
            </TableRow>
        );
      });
    }
    
    return (
        <TableRow>
          <TableCell colSpan={3} className="text-center text-muted-foreground p-4">
            <div className="flex flex-col items-center space-y-2">
              <p>No trades to display.</p>
              <Button 
                onClick={handleManualRefresh} 
                variant="outline" 
                size="sm"
                disabled={isRefreshing || !isOnline}
              >
                <RefreshCw className={cn("h-4 w-4 mr-1", isRefreshing && "animate-spin")} />
                Refresh
              </Button>
            </div>
          </TableCell>
        </TableRow>
    );
  }, [isLoading, error, trades.length, visibleTrades, newTradeIds, isRefreshing, isOnline, handleManualRefresh]);

  return (
    <Card className={cn("", className)}>
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            <span>Recent Trades</span>
            {lastUpdated && (
              <span className="text-xs font-normal text-muted-foreground">
                Updated: {formatLastUpdated(lastUpdated)}
              </span>
            )}
          </div>
          
          <div className="flex items-center space-x-2">
            <Button
              variant="ghost"
              size="sm"
              onClick={toggleAutoRefresh}
              className="h-8 w-8 p-0"
              title={pauseAutoRefresh ? "Resume auto refresh" : "Pause auto refresh"}
            >
              {pauseAutoRefresh ? (
                <div className="h-2 w-2 rounded-full bg-red-500" />
              ) : (
                <div className="h-2 w-2 rounded-full bg-green-500" />
              )}
            </Button>
            
            <Button
              variant="ghost"
              size="sm"
              onClick={handleManualRefresh}
              disabled={isRefreshing || !isOnline}
              className="h-8 w-8 p-0"
              title="Refresh data"
            >
              <RefreshCw className={cn("h-4 w-4", isRefreshing && "animate-spin")} />
            </Button>
            
            {isOnline ? (
              <Wifi className="h-4 w-4 text-green-500" title="Online" />
            ) : (
              <WifiOff className="h-4 w-4 text-red-500" title="Offline" />
            )}
          </div>
        </CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[200px]" ref={scrollAreaRef}>
          <Table>
            <TableHeader className="sticky top-0 bg-background">
              <TableRow className="text-xs">
                <TableHead className="p-2 w-[33%]">Time</TableHead>
                <TableHead className="p-2 w-[33%]">Price (USDT)</TableHead>
                <TableHead className="p-2 text-right w-[33%]">Amount</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {renderContent}
            </TableBody>
          </Table>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
