'use client';

import { useState, useEffect } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { getOrderBookData, getMarketData } from '@/lib/actions';
import type { OrderBook, OrderBookEntry, TickerInfo } from '@/lib/bybit-api';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Skeleton } from '@/components/ui/skeleton';

interface OrderBookProps {
  symbol: string;
}

export function OrderBook({ symbol }: OrderBookProps) {
  const [data, setData] = useState<OrderBook | null>(null);
  const [marketData, setMarketData] = useState<TickerInfo | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchData = async (isInitialLoad = false) => {
      if (isInitialLoad) {
        setIsLoading(true);
        setError(null);
      }
      try {
        const [orderBookData, market] = await Promise.all([
            getOrderBookData(symbol),
            getMarketData(symbol)
        ]);

        if (orderBookData) {
          setData(orderBookData);
        } else if(isInitialLoad) {
          throw new Error('No order book data received.');
        }

        if (market) {
            setMarketData(market);
        } else if (isInitialLoad) {
          console.warn(`Could not fetch market price for ${symbol}`);
        }
        if (isInitialLoad) setError(null);

      } catch (e) {
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
        if (isInitialLoad) {
            setError(errorMessage);
        }
        console.error(`Failed to fetch order book for ${symbol}:`, e);
      } finally {
        if (isInitialLoad) {
            setIsLoading(false);
        }
      }
    };

    fetchData(true);
    const intervalId = setInterval(() => fetchData(false), 3000); // Poll every 3 seconds

    return () => clearInterval(intervalId);
  }, [symbol]);

  const renderRows = (entries: OrderBookEntry[] | undefined, type: 'bid' | 'ask', maxTotal: number) => {
    if (!entries) return null;
    
    const sortedEntries = type === 'ask' ? [...entries].reverse() : entries;

    let cumulativeTotal = 0;
    const dataWithTotal = sortedEntries.map(([price, size]) => {
        cumulativeTotal += parseFloat(size);
        return {price: parseFloat(price), size: parseFloat(size), total: cumulativeTotal};
    });
    
    return dataWithTotal.map(({price, size, total}, index) => {
        const depth = maxTotal > 0 ? (total / maxTotal) * 100 : 0;
        const priceColor = type === 'bid' ? 'hsl(var(--chart-2))' : 'hsl(var(--chart-5))';
        const depthBg = type === 'bid' ? 'hsla(var(--chart-2), 0.2)' : 'hsla(var(--chart-5), 0.2)';

        return (
            <TableRow key={index} className="text-xs">
                <TableCell className="p-1 font-mono text-left relative" style={{ color: priceColor }}>
                     <div className={`absolute top-0 h-full ${type === 'bid' ? 'left-0' : 'right-0'}`} style={{ width: `${depth}%`, background: depthBg, zIndex: -1}}></div>
                    {price.toFixed(2)}
                </TableCell>
                <TableCell className="p-1 font-mono text-right">{size.toFixed(4)}</TableCell>
                <TableCell className="p-1 font-mono text-right">{total.toFixed(4)}</TableCell>
            </TableRow>
        );
    });
  };
  
  const LoadingRows = () => (
    Array.from({ length: 10 }).map((_, i) => (
        <TableRow key={i} className="text-xs">
            <TableCell className="p-1"><Skeleton className="h-4 w-full" /></TableCell>
            <TableCell className="p-1"><Skeleton className="h-4 w-full" /></TableCell>
            <TableCell className="p-1"><Skeleton className="h-4 w-full" /></TableCell>
        </TableRow>
    ))
  );

  const ErrorRow = ({ message }: { message: string }) => (
    <TableRow>
      <TableCell colSpan={3} className="text-center text-red-500 p-4">
        Error: {message}
      </TableCell>
    </TableRow>
  );

  const bidsTotal = data?.bids.reduce((acc, [, size]) => acc + parseFloat(size), 0) || 0;
  const asksTotal = data?.asks.reduce((acc, [, size]) => acc + parseFloat(size), 0) || 0;
  const maxTotal = Math.max(bidsTotal, asksTotal);


  return (
    <Card>
      <CardHeader>
        <CardTitle>Order Book</CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[400px]">
          {isLoading ? (
             <div className="p-4"><Skeleton className="h-80 w-full" /></div>
          ) : error ? (
            <div className="p-4 text-center text-red-500">{error}</div>
          ) : (
            <div>
              <Table>
                <TableHeader>
                  <TableRow className="text-xs">
                    <TableHead className="p-1 text-left w-[33%]">Price (USDT)</TableHead>
                    <TableHead className="p-1 text-right w-[33%]">Size ({symbol.replace('USDT', '')})</TableHead>
                    <TableHead className="p-1 text-right w-[33%]">Total</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {renderRows(data?.asks, 'ask', maxTotal)}
                </TableBody>
              </Table>
              <div className="text-lg font-bold text-center py-2 border-t border-b">
                {marketData?.lastPrice ? parseFloat(marketData.lastPrice).toFixed(2) : <Skeleton className="h-6 w-1/4 mx-auto" />}
              </div>
              <Table>
                 <TableHeader>
                  <TableRow className="text-xs">
                    <TableHead className="p-1 text-left w-[33%]">Price (USDT)</TableHead>
                    <TableHead className="p-1 text-right w-[33%]">Size ({symbol.replace('USDT', '')})</TableHead>
                    <TableHead className="p-1 text-right w-[33%]">Total</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {renderRows(data?.bids, 'bid', maxTotal)}
                </TableBody>
              </Table>
            </div>
          )}
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
