'use client';

import { useState, useEffect } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { TrendingUp, TrendingDown, BarChart, AlertCircle, Percent } from 'lucide-react';
import { getMarketData } from '@/lib/actions';
import type { TickerInfo } from '@/lib/bybit-api';
import { Skeleton } from '@/components/ui/skeleton';
import { cn } from '@/lib/utils';

interface MarketDataProps {
  symbol: string;
}

export function MarketData({ symbol }: MarketDataProps) {
  const [data, setData] = useState<TickerInfo | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchData = async (isInitialLoad = false) => {
      if (isInitialLoad) {
        setIsLoading(true);
        setError(null);
      }
      try {
        const marketData = await getMarketData(symbol);
        if (marketData) {
          setData(marketData);
          setError(null);
        } else if(isInitialLoad) {
          throw new Error('No market data received.');
        }
      } catch (e) {
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
        if(isInitialLoad) {
            setError(errorMessage);
        }
        console.error(`Failed to fetch market data for ${symbol}:`, e);
      } finally {
        if (isInitialLoad) {
          setIsLoading(false);
        }
      }
    };

    fetchData(true);
    const intervalId = setInterval(() => fetchData(false), 5000); // Poll every 5 seconds

    return () => clearInterval(intervalId);
  }, [symbol]);

  const formatNumber = (numStr: string | undefined) => {
    if (!numStr) return '0.00';
    const num = parseFloat(numStr);
    if (num > 1000) {
      return num.toLocaleString(undefined, { maximumFractionDigits: 0, minimumFractionDigits: 0 });
    }
    return num.toLocaleString(undefined, { maximumFractionDigits: 2, minimumFractionDigits: 2 });
  };
  
  const formatVolume = (numStr: string | undefined) => {
    if (!numStr) return '0';
    const num = parseFloat(numStr);
    if (num > 1_000_000) {
        return `${(num / 1_000_000).toFixed(2)}M`;
    }
    if (num > 1_000) {
        return `${(num / 1_000).toFixed(1)}K`;
    }
    return num.toFixed(2);
  }

  const formatPercent = (percentStr: string | undefined) => {
    if (!percentStr) return '0.00';
    const num = parseFloat(percentStr) * 100;
    return num.toFixed(2);
  }

  const pricePcnt = data?.price24hPcnt ? parseFloat(data.price24hPcnt) : 0;
  const isPositive = pricePcnt >= 0;

  const renderCardContent = (value: string | undefined, formatter: (val: string | undefined) => string, prefix = '', suffix = '') => {
    if (isLoading) return <Skeleton className="h-8 w-3/4" />;
    return <div className="text-2xl font-bold">{prefix}{formatter(value)}{suffix}</div>;
  }

  return (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
      <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Last Price</CardTitle>
            <span className={cn('text-sm font-semibold', isPositive ? 'text-chart-2' : 'text-chart-5')}>
                {isPositive ? '+' : ''}{formatPercent(data?.price24hPcnt)}%
            </span>
          </CardHeader>
          <CardContent>
            {renderCardContent(data?.lastPrice, formatNumber, '$')}
            <p className="text-xs text-muted-foreground">
                24h change
            </p>
          </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">24h High</CardTitle>
          <TrendingUp className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          {renderCardContent(data?.highPrice24h, formatNumber, '$')}
          <p className="text-xs text-muted-foreground">Highest price in last 24 hours</p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">24h Low</CardTitle>
          <TrendingDown className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          {renderCardContent(data?.lowPrice24h, formatNumber, '$')}
          <p className="text-xs text-muted-foreground">Lowest price in last 24 hours</p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">24h Volume</CardTitle>
          <BarChart className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          {renderCardContent(data?.volume24h, formatVolume, '', ` ${symbol.replace('USDT', '')}`)}
          <p className="text-xs text-muted-foreground">Total volume traded in last 24 hours</p>
        </CardContent>
      </Card>
    </div>
  );
}
